@import "reset.scss";

$white: #fff;

.container{
    max-width: 1200px;
    margin: 0 auto;
}


body, html{
    overflow-x: hidden;
}

// index.html
.main-page{
    background: url(../imagemin/background.png) center center no-repeat;
    background-size: cover;
}

h1{
    margin: 0 auto;
    width: 380px;
    font-size: 45px;
    font-weight: bold;
    text-align: center;
    color: $white;
    padding: 30px 0 70px;
}

nav{
    margin: 0 auto;
    width: 100%;
}

#checkbox-menu,
.toggle{
    display: none;
}

/* это ul */
.menu{
    padding: 0;
    margin: 0;
    height: 100px; /* попробой не ставить высоту */
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    list-style-type: none;
}

.menu li a{
    color: $white;
    text-decoration: none;
    align-self: center;
    font-size: 30px;
    padding: 10px 15px;
    transition: background .3s linear, color .3s linear;
    border-radius: 5px;
}

.menu li a:hover{
    background: $white;
    border-radius: 5px;
    color: #000;
}




.main-text{
    text-align: center;
    color: $white;
    font-size: 26px;
    margin-bottom: 103px;
    margin-top: 50px;
    padding: 10px;
        border: 1px solid #ccc;
        border-radius: 5px;
        &:hover{
            background-color: darken(rgb(22, 21, 21), 10%);
            color: $white;
        }
}

.paphos-text{
    color: $white;
    padding-bottom: 40px;
    text-align: center;
    font-size: 30px;
    font-style: italic;
    border-bottom: 1px solid #fff;
}

.contacts{
    border-radius: 20px;
    margin: 0 auto;
    font-size: 28px;
    color: $white;
    padding: 10px;
    text-align: center;
    margin: 30px 0;
}





.phone{
    border: 1px solid #ccc;
    border-radius: 20px;
    max-width: 338px;
    margin: 0 auto;

    i,p,a{
        display: inline-block;
        font-size: 24px;
        color: $white;
        padding: 10px;
    }
    p{
        font-size: 26px;
    }
    i{
        color: rgba(255,255,0,.835);
        font-size: 26px;
        padding-right: 5px;
        position: relative;
        top: 2px;
    }
    a{
        &:hover{
            text-decoration: underline;
        }
    }

}














button {
    padding: 0;
    border: none;
    font: inherit;
    color: inherit;
    background-color: transparent;
    /* отображаем курсор в виде руки при наведении; некоторые
    считают, что необходимо оставлять стрелочный вид для кнопок */
    cursor: pointer;
  }






// --------------------------
//services
//---------------------------
.service__titles{
    display: flex;
    justify-content: space-between;
    max-width: 650px;
    margin-left: 20px;
    margin-bottom: 20px;
    padding-top: 10px;
    align-items: center;
}

.service__subtitle1{
    transition: border-color .2s linear;
    font-size: 46px;
    color: #fff;
    text-align: center;
    padding: 5px;
    border: 1px solid #212121;
    border-radius: 10px;
    transition: box-shadow .2s linear;
}

.service__subtitle1:hover{
    box-shadow: 1px 1px 10px #ccc;
    border-color: #ccc;
}


.service__subtitle2{
    font-size: 35px;
    color: #fff;
    text-align: left;
}

.service__subtitle2 a{
    color: inherit;
}

.service__subtitle2 a:hover{
    text-decoration: underline;
}

body{
    background-image: linear-gradient(to right top, #1f1f1f, #1d1d20, #1b1b22, #181a23, #141825, #0f1724, #0a1723, #041622, #01151d, #011418, #031213, #060f0e);}
.first{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    margin-bottom:20px;
}


button {
    padding: 0;
    border: none;
    font: inherit;
    color: inherit;
    background-color: transparent;
    /* отображаем курсор в виде руки при наведении; некоторые
    считают, что необходимо оставлять стрелочный вид для кнопок */
    cursor: pointer;
  }

.blog{
    max-width: 1200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 15px;
}

.services__item{
    width: 370px;
    height: 459px;
    border: 2px solid #000;
    border-radius: 10px;
    overflow: hidden;
    position: relative;
    transition: box-shadow 0.2s linear;
}

.services__item:hover{
    border: 2px solid #fff;
    box-shadow: 1px 1px 10px #ccc;  
    background-color: #212121;
}
    
.services__item-img{
    width: inherit;
}

.services__item-image{
    max-width: 100%;
    height: auto;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    display: block;
    object-fit: cover;
    transition: transform .3s ease-in-out;
}

.services__item-image:hover{
    transform: scale(1.3);
    transition-duration: .9s;
}

.services__item-bottom{
    width: 370px;
    text-align: center;
}

.services__item-footer{
    display: flex;
    justify-content: space-around;
}

.services__item-footer button{
    margin-right: -140px;
}

.services__item-service p{
    color: #fff;
    padding: 5px;
    border-right: 1px solid #212121;
    border-left: 1px solid #212121;
    border-radius: 20px;
    transition: background-color 0.2s linear;
    transition: color 0.2s linear;
    font-size: 26px;
    border: 2px solid #000;
    margin-top: 8px;
    margin-bottom: 8px;
    transition: background .2s linear;
}

.services__item-service p:hover{
    background: rgb(19, 18, 18);
    color: #ccc;
}

.services__item-footer{
    border: 2px solid #000;
    border-radius: 20px;
}   

.services__item-footer button{
    transition: background-color .2s ease;
    padding: 8px 10px;
    font-size:24px;
    font-weight: 700;
    color: #ccc;
    border: 1px solid #212121;
    border-radius: 30px;
    background-color: black;
    transition: box-shadow .2s linear;
}

.services__item-footer button:hover{
    color: #fff;
    background-color: rgb(10, 1, 1);
    text-decoration: underline;
    box-shadow: 1px 1px 10px #ccc;
}

.services__item-footer p{
    color: #fff;
    font-size: 22px;
    font-style: italic;
    line-height: 2;
}

.list__footer{
    border: 2px solid #ccc;
    border-radius: 20px;
    margin: 0 auto 30px auto;
    text-align: center;
    max-width: 300px;
    &:hover{
        box-shadow: 1px 1px 10px #ccc;
        border-color: #ccc;
    }
    li{
        margin-bottom: 15px;
        &:last-child{
            padding-bottom: 10px;
        }
        &:first-child{
            padding-top: 22px;
        }
    }
    a{
        font-size: 24px;
        color: #fff;
        text-decoration: none;
        
        &:hover{
            text-decoration: underline;
        }
        &:first-child{
            font-size: 30px;
        }
    }
}

footer{
    border-top: 1px solid #fff;
    p{
        font-size: 28px;
        color: #fff;
        text-align: center;
        padding: 15px;
    }
}

.footer__index{
   margin-top: 40px;
   padding-top: 20px;
   padding-bottom: 20px;
}

.header__services{
    display: none;
    visibility: hidden;
    opacity: 0;
}















//===============================
//zone
//===============================

.map__icon{
    margin: 20px 0 15px;
    display: flex;
    justify-content: center;
    i{
        font-size: 30px;
        color: rgba(255, 255, 0, 0.835);
    }
}

.location__text{
    font-size: 26px;
    color: #fff;
    text-align: center;
    padding: 5px;
    text-decoration: underline;
    margin-bottom: 35px;
}

.location__map{
    position: relative;
    transition: box-shadow .2s linear;
    margin: 0 auto 35px auto;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #000;
    border-radius: 20px;
    overflow: hidden;
    object-fit: cover;
    width: 460px;
    height: 400px;
    transition: border-color .2s linear;
    &:hover{
        border-color: #ccc;
        box-shadow: 1px 1px 10px #ccc;
    }
    img{
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        z-index: 1;
        max-width: 100%;
        height: auto;
        transition: transform .4s ease-in-out;
        &:hover{
            transform: scale(1.3);
            transition-duration: 1s;
        }
    }
}




//================================
//modal
//================================
.modal{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1000;
    background-color: rgba(black, .8);
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 10px;
    display: none;
    &.show{
        display: flex;
    }
    text-align: center;
}


.modal__content{
    max-width: 400px;
    padding: 40px 0;
    background-color: #2e3e55;
    border-radius: 5px;
    width: 100%;
    position: relative;
    transform: translateY(-200px);
    opacity: 0;
    transition: transform 200ms ease-in-out, opacity ease-in-out 200ms;
}

.modal__phone{
    color: #fff;
    i{
        color: rgba(255,255,0,.835);
        padding: 5px;
        font-size: 22px;
    }
    a{
        color: inherit;
        padding: 5px;
        font-size: 24px;
    }
}

.modal__name{
    color: #fff;
    font-size: 24px;
    margin-top: 20px;
}

.modal__close{
    position: absolute;
    top: 2rem;
    right: 2rem;
    background: none;
    padding: 0;
    border: none;
    cursor: pointer;
    opacity: .5;
    transition: transform .2s linear, opacity .1s linear;
    &:hover{
        transform: rotate(180deg);
        opacity: 1;
    }
}

































@media screen and (max-width: 1190px){
    .first{
        grid-template-columns: 1fr 1fr;
    }

    .blog:nth-of-type(odd){
        margin-right: -40px;
    }

    .blog:nth-of-type(even){
        margin-left: -40px;
    }

    .service__titles{
        max-width: 500px;
        margin-left: 120px;
    }

}

@media screen and (max-width: 975px){
    .blog:nth-of-type(odd){
        margin-right: 0px;
    }

    .blog:nth-of-type(even){
        margin-left: 0px;
    }

    .service__titles{
        max-width: 500px;
        margin-left: 70px;
    }
}


@media screen and (max-width: 850px){
    .first{
        grid-template-columns: 1fr;
    }

    .service__titles{
        max-width: 430px;
        margin-left: 70px;
    }

    .blog:nth-of-type(odd){
        margin-right: 0px;
        margin-left: 0;
    }

    .blog:nth-of-type(even){
        margin-left: 0px;
        margin-right: 0;
    }

    .header__services{
        display: block;
        visibility: visible;
        opacity: 1;
    }

    .service__titles{
        display: none;
        visibility: hidden;
        opacity: 0;
    }

    .list__footer--reviews{
        display: none;
    }
}



@media screen and (max-width: 765px){
    h1{
        display: none;
        visibility: hidden;
        opacity: 0;
    }

    .toggle{
        clear: both;
        display: block;
        text-align: center;
        font-size: 28px;
        font-weight: 900;
        line-height: 40px;
        width: 100%;
        color: #000;
        background-color: #ccc;
        transition: all .1s linear;
    }

    .toggle:hover{
        box-shadow: 1px 1px 10px#ccc;
    }
    #checkbox-menu:checked + label .menu li{
        opacity: 1;
        visibility: visible;
        transition: all .7s linear;
    }

    #checkbox-menu:checked + label .menu{
        height: 250px;
    }

    .menu{
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        height: 0px;
        transition: height .3s linear;
    }
    .menu li{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 95%;
        opacity: 0;
        visibility: hidden;
    }
    .menu li a{
        width: 95%;
        text-align: center;
        align-self: center;
        align-content: center;
    }

    .main-text {
        font-size: 22px;
        margin-bottom: 73px;
        margin-top: 90px;
    }

    .paphos-text {
        font-size: 24px;
    }
    .footer__index {
        padding-bottom: 50px;
        font-size: 22px;

    }
    footer p{
        font-size: 22px;
    }

    .first{
        grid-template-columns: 1fr;
        align-items: center;
    }

    .service__titles{
        justify-content: center;
        width: 100%;
        max-width: 200px;
        align-items: center;
        text-align: center;
        margin: 0 auto;
        padding: 15px;
    }

    .service__subtitle2{
        display: none;
        visibility: hidden;
        opacity: 0;
    }

    .service__subtitle1{
        text-align: center;
    }

    .first{
        margin-top: 30px;
    }

}





@media screen and (max-width: 575px){
    .toggle{
        font-size: 24px;
    }

    .footer__index {
        padding-bottom: 50px;
        font-size: 18px;

    }
    .footer p{
        font-size: 18px;
    }

    .menu li a{
        font-size: 24px;
    }

    .zone__title h2{
        font-size: 34px;
    }

    .location__map{
        width: 380px;
        height: 330px;
    }

}

@media screen and (max-width: 420px){
    .services__item{
        width: 320px;
        height: 396px;
    }

    .services__item-service p{
        padding: 7px;
        font-size: 21px;
    }

    .services__item-bottom{
        width: 320px;
    }

    .services__item-footer button{
        margin-right: -80px;
    }

    .zone__title h2{
        font-size: 29px;
        margin-top: 15px;
    }
    .location__text{
        font-size: 24px;
    }

    .location__map{
        width: 280px;
        height: 240px;
    }
    .list__footer a:first-child {
        font-size: 24px;
    }

    .list__footer {
        max-width: 200px;
    }
}


@media screen and (max-width: 380px){
    .contacts{
        font-size: 24px;
    }

    .phone a{
        max-width: 294px;
    }

    .phone a{
        font-size: 20px;
    }

    .phone p{
        font-size: 22px;
    }

    .modal__name {
        margin-top: 10px;
    }

    .modal__phone{
        margin-top: 20px;
    }

    .modal__close{
        top: 1rem;
        right: 1rem;
    }
}






@media screen and (max-width: 350px){
    .services__item{
        width: 280px;
        height: 345px;
    }

    .services__item-bottom{
        width: 280px;
    }

    .services__item-footer button{
        margin-right: -50px;
    }
}